
import { Component, Emit, Prop, Vue } from "vue-property-decorator";

@Component({ name: "ui-button-dialog" })
export default class UiButtonDialog extends Vue {
  @Prop({ type: String, required: true }) dialogTitle!: string;
  @Prop({ type: String, default: "" }) dialogText!: string;
  @Prop({ type: String, default: "" }) dialogButtonText!: string;
  @Prop({ type: String, default: "" }) cancelButtonText!: string;
  @Prop({ type: String, default: "" }) confirmButtonText!: string;

  @Emit("cancel")
  onCancel() {
    this.dialog = false;
    return;
  }

  @Emit("confirm")
  onConfirm() {
    return;
  }

  dialog = false;
}
